@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@500;700&display=swap);
.nav-bar {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: space-between;
          justify-content: space-between;
  -webkit-align-self: stretch;
          align-self: stretch;
}

.menu-nav {
  width: 350px;
  border: 1px solid black;
  background-color: hsl(var(--primary-dark-violet));
  border-radius: 10px;
  justify-self: center;
}

.menu-nav ul {
  list-style: none;
  padding: 0;
  margin: 0;
}

.menu-nav li {
  text-align: center;
  margin: 2rem 0;
}

.menu-nav a {
  text-decoration: none;
  color: white;
  font-weight: 700;
  font-size: 20px;
}

.nav-logo {
  margin: 0;
}

.menu-icon svg {
  opacity: 0.5;
}

hr {
  height: 1px;
  border: 0;
  background-color: hsla(var(--primary-dark-violet), 1);
  -webkit-filter: brightness(140%);
          filter: brightness(140%);
  width: 85%;
}

.btn {
  border: none;
}

.btn:active {
  outline: none;
}

.btn--round {
  border-radius: 20px;
}

.btn--regular {
  border-radius: 10px;
}

.btn--mobile {
  text-align: center;
  width: 80%;
  height: 2.5rem;
}

.btn--small {
  width: 80px;
}

.btn--medium {
  width: 150px;
}

.cyan {
  background-color: hsl(var(--primary-cyan));
  color: white;
}

.violet {
  background-color: hsl(var(--primary-dark-violet));
  color: white;
}

.cyan:hover {
  background-color: hsla(var(--primary-cyan), 0.5);
  transition: all 0.3s ease;
}

:root {
  --primary-cyan: 180, 66%, 49%;
  --primary-dark-violet: 257, 27%, 26%;
  --secondary-red: hsl(0, 87%, 67%);
  --neutral-gray: hsl(0, 0%, 75%);
  --neutral-grayish-violet: hsl(257, 7%, 63%);
  --neutral-very-dark-blue: hsl(255, 11%, 22%);
  --neutral-very-dark-violet: hsl(260, 8%, 14%);
  --button-hover: hsla(180, 66%, 49%, 0.9);
}

* {
  font-family: "Poppins", sans-serif;
  font-size: 18px;
  box-sizing: border-box;
}

.container {
  margin: 1rem;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-flow: column;
          flex-flow: column;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
}

.attribution {
  font-size: 11px;
  text-align: center;
}
.attribution a {
  color: hsl(228, 45%, 44%);
}

